"use client";

import React from "react";

interface ExamQuestionProps {
  index: number;
  question: {
    question: string;
    options: string[];
    correctAnswer: string;
  };
  selectedAnswer: string;
  onSelectAnswer: (index: number, selectedOption: string) => void; // ‚úÖ Definir tipo correctamente
}

const ExamQuestion: React.FC<ExamQuestionProps> = ({
  index,
  question,
  selectedAnswer,
  onSelectAnswer,
}) => {
  console.log("‚úÖ ExamQuestion: onSelectAnswer recibido:", typeof onSelectAnswer);

  if (typeof onSelectAnswer !== "function") {
    console.error("‚ùå Error: onSelectAnswer no es una funci√≥n.");
    return <p className="text-red-500">‚ö†Ô∏è Error en la funci√≥n onSelectAnswer</p>;
  }

  return (
    <div className="mb-4 p-4 border rounded-lg shadow-md">
      <h2 className="text-lg font-semibold">{question.question}</h2>

      <div className="flex flex-col mt-2">
        {question.options.map((option) => (
          <button
            key={option}
            onClick={() => {
              console.log(`‚ÑπÔ∏è Click en opci√≥n: ${option} de la pregunta ${index}`);
              onSelectAnswer(index, option); // ‚úÖ Se ejecuta la funci√≥n
            }}
            className={`px-4 py-2 border rounded-md mt-1 transition-all ${
              selectedAnswer === option
                ? "bg-blue-500 text-white"
                : "bg-gray-100 hover:bg-gray-200"
            }`}
          >
            {option}
          </button>
        ))}
      </div>
    </div>
  );
};

export default ExamQuestion;
