"use client";

import React, { useEffect, useState } from "react";
import Image from "next/image";
import { useTheme } from "next-themes";
import Link from "next/link";

interface Resource {
  id: number;
  name: string;
  description?: string;
}

const ResourcesList = ({
  resources,
  showAllLink,
}: {
  resources: Resource[];
  showAllLink: string;
}) => {
  const { theme } = useTheme();
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    setMounted(true);
  }, []);

  if (!mounted) return null; // üîπ Evita renderizar hasta que el cliente est√© listo

  const resourceIcon =
    theme === "dark"
      ? "/assets/dark/resources-icon.svg"
      : "/assets/light/resources-icon.svg";

  return (
    <div className="p-4 border rounded-lg shadow-md bg-white dark:bg-gray-800">
      <div className="flex items-center justify-between mb-4">
        <h3 className="font-semibold text-lg text-neutral-700 dark:text-white flex items-center gap-2">
          <Image src={resourceIcon} alt="Recursos" width={24} height={24} />
          Recursos
        </h3>
        <Link
          href={showAllLink}
          className="text-primary hover:underline text-sm font-medium dark:text-blue-400"
        >
          Ver todo
        </Link>
      </div>

      <ul className="space-y-4">
        {resources.map((resource) => (
          <li key={resource.id} className="flex items-center gap-2">
            <Link href={`/resources/${resource.id}`} className="flex items-center gap-2 w-full">
              <div className="w-10 h-10 bg-gray-200 dark:bg-gray-600 rounded"></div>
              <div>
                <p className="text-sm font-bold text-neutral-700 dark:text-white">
                  {resource.name}
                </p>
                <p className="text-xs text-neutral-500 dark:text-gray-300">
                  {resource.description || "Sin descripci√≥n"}
                </p>
              </div>
            </Link>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default ResourcesList;
